<template>
  <div class="iframe_box">
    <!-- <vue-friendly-iframe :src="src" @load="onLoad"></vue-friendly-iframe> -->
    <pikaz-iframe :setting="setting" @onload="onload"> </pikaz-iframe>
  </div>
</template>

<script>
import { PikazIframe } from "pikaz-iframe";
export default {
  components: {
    PikazIframe,
  },
  data() {
    return {
      src: this.$route.query.link,
      setting: {
        src: this.$route.query.link, //被嵌套的页面的 URL 地址
        frameborder: 0, // 是否显示框架周围的边框 0/1
        sandbox: "", //启用iframe中内容的额外限制,传入空字符串则对所有权限做限制
        allowfullscreen: true, //设置为true时，可以通过调用 <iframe> 的 requestFullscreen() 方法激活全屏模式。
        // srcdoc: "", //html内容
        hideScrolling: true, //是否隐藏滚动条，传入true默认隐藏18px宽的滚动条，如需隐藏其他宽度滚动条，则传入宽度如"20px"
        css: "", //传入html内容时，需修改的css样式，如"div{color:red;}"
      },
      onload: () => {
        console.log("载入成功");
      },
    };
  },
  mounted() {},
  methods: {},
};
</script>
<style lang="scss" scoped>
.iframe_box {
  width: 100%;
  height: 100vh;
}
/deep/ .vue-friendly-iframe {
  height: 100vh;
}
/deep/ iframe {
  width: 100%;
  height: 100vh;
}
</style>



